.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 40px;
  background-color: #1b2d37; }

@media (max-width: 1023px) {
  .container {
    padding: 0 25px; } }

@media (max-width: 767px) {
  .container {
    padding: 0; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 1428px;
  margin: 0 auto;
  color: #ffffff; }

.main-nav__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  margin: 26px 0 0 0; }

.main-nav__item:last-child {
  margin: 0 0 0 71px; }

.main-nav__link {
  line-height: 18px;
  padding: 0 0 0 50px;
  color: #ffffff; }
  .main-nav__link--mail, .main-nav__link--phone {
    position: relative; }
  .main-nav__link--mail::before {
    content: "";
    position: absolute;
    width: 19px;
    height: 14px;
    left: 11px;
    top: 3px;
    background-image: url("../img/mail-icon.svg");
    background-repeat: no-repeat; }
  .main-nav__link--phone::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 18px;
    left: 17px;
    top: 1px;
    background-image: url("../img/phone-icon.svg");
    background-repeat: no-repeat; }
  .main-nav__link:hover {
    opacity: 0.7; }
  .main-nav__link:active {
    opacity: 0.3; }

.main-nav__button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
  letter-spacing: 0.05em;
  width: 180px;
  margin: 15px 17px 16px 50px;
  background-color: transparent;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 5px;
  cursor: pointer; }
  .main-nav__button:hover {
    opacity: 0.7;
    border-color: #00b2ff; }
  .main-nav__button:active {
    opacity: 0.3;
    border-color: #00b2ff; }

@media (max-width: 1023px) {
  .main-nav__button {
    display: none; }
  .main-nav__link span {
    display: none; }
  .main-nav__item:last-child {
    margin: 0 0 0 33px; }
  .main-nav__link--mail::before {
    left: 50px;
    top: -1px;
    width: 25px;
    height: 19px;
    background-image: url("../img/mail-tablet.svg"); }
  .main-nav__link--phone::before {
    left: 25px;
    top: -4px;
    width: 24px;
    height: 24px;
    background-image: url("../img/phone-tablet.svg"); } }

@media (max-width: 767px) {
  .main-nav__link--mail::before {
    left: 28px;
    top: -3px; }
  .main-nav__link--phone::before {
    left: -7px;
    top: -6px; } }

