html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;
  color: #000000; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.main-nav__logo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-right: auto;
  cursor: pointer; }
  .main-nav__logo:hover img,
  .main-nav__logo:hover p {
    opacity: 0.7; }
  .main-nav__logo:active img,
  .main-nav__logo:active p {
    opacity: 0.3; }

.main-nav__logo b {
  display: block;
  min-width: 150px;
  margin-bottom: 2px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff; }

.main-nav__logo span {
  display: block;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

.main-nav__logo img {
  margin-right: 16px; }

@media (max-width: 767px) {
  .main-nav__logo b,
  .main-nav__logo span {
    display: none; }
  .main-nav__logo img {
    width: 38px;
    height: 38px;
    margin-top: 17px; } }
